.container {
  max-width: 480px;
  min-width: 320px;

  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;

  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding-left: 30px;
    padding-right: 30px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 1280px;
    padding-left: 68px;
    padding-right: 68px;
  }
}
